
.datepicker {
  position: relative;
  display: inline-block;
  font-size: 13px;
}
.datepicker>input[type="text"] {
  cursor: pointer;
  width: 232px;
  border-radius: 2px;
  border: 1px solid #ddd;
  font-size: 16px;
  height: 30px;
}
.datepicker>input[type="text"]:hover {
  border: 1px solid #468df1;
}
.datepicker-popup {
  position: absolute;
  border: 1px solid #ccc;
  background: #fff;
  margin-top: 2px;
  z-index: 1000;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, .1);
}
.datepicker-inner {
  width: 250px;
}
.datepicker-ctrl {
  position: relative;
  height: 35px;
  line-height: 35px;
  font-weight: bold;
  text-align: center;
}
.datepicker-ctrl p, .datepicker-ctrl span, .datepicker-body span {
  display: inline-block;
  width: 35px;
  line-height: 35px;
  height: 35px;
  border-radius: 35px;
}
.datepicker-ctrl p {
  width: 65%;
  margin: 0px;
}
.datepicker-ctrl span {
  position: absolute;
}
.datepicker-body span {
  text-align: center;
}
.datepicker-mouthRange span {
  width: 44px;
  height: 44px;
  line-height: 44px;
  margin: 2px 18px;
}
.datepicker-item-disable {
  background-color: white !important;
  cursor: not-allowed !important;
}
.datepicker-datepicker-decadeRange span:first-child,
.datepicker-datepicker-decadeRange span:last-child,
.datepicker-item-disable,
.datepicker-item-gray{
  color: #ccc;
}
.datepicker-dateRange-item-active:hover,
.datepicker-dateRange-item-active {
  background: #468df1 !important;
  color: #fff !important;
}
.datepicker-mouthRange {
  margin-bottom: 10px;
  margin-left:10px;
}
.datepicker-mouthRange span,
.datepicker-ctrl span,
.datepicker-ctrl p,
.datepicker-dateRange span {
  transition: all .3s ease;
  cursor: pointer;
}
.datepicker-mouthRange span:hover,
.datepicker-ctrl p:hover,
.datepicker-ctrl i:hover,
.datepicker-dateRange span:hover,
.datepicker-dateRange-item-hover {
    background-color : #f5f9ff;
}
.datepicker-weekRange{
  border-bottom: 1px solid #eee;
  border-top: 1px solid #eee;
}
.datepicker-weekRange span{
    font-weight: bold;
}
.datepicker-month-btn{
  font-weight: bold;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
}
.datepicker-preBtn{
  left: 2px;
}
.datepicker-nextBtn{
  right: 2px;
}

/*fadeDown*/
.fadeDown-enter-active,.fadeDown-leave-active {
  transition: all .3s ease;
}
.fadeDown-enter, .fadeDown-leave-active {
  transform:translateY(-10px);
  opacity: 0;
}



