
.popover {
  position: absolute;
  background: #fafafa;
  color: #222;
  border: 1px solid rgba(204, 204, 204, 1);
  z-index: 2000;
}
.slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .3s ease;
}
.slide-fade-enter, .slide-fade-leave-active {
  transform: translate(5px);
  opacity: 0;
}



